import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select } from 'antd';
import { scholarshipRatings } from 'dump/data';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: 0,
    system: '',
    nameRating: '',
    codeRating: '',
    min10: 0,
    min4: 0,
    minTrainingPoints: 0,
    totalMoney: 0,
  };
  return { ...initialValueDefault, ...initialValue };
};

const { Option } = Select;

@Form.create({ name: 'formScholarshipRatings' })
class FormScholarshipRatingsPage extends Component {
  state = {
    scholarshipRatingsId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ scholarshipRatingsId: match.params.id });
      this.getScholarshipRatins(match.params.id);
    }
  };

  getScholarshipRatins = code => {
    const scholarshipMap = scholarshipRatings.filter(object => object.codeRating === code)[0];
    const initialValue = handleInitialValue(scholarshipMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { scholarshipRatingsId } = this.state;
    return (
      <Card title={scholarshipRatingsId ? 'Cập nhật xếp loại học bổng' : 'Thêm xếp loại học bổng'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Hệ" {...formItemLayout}>
              {getFieldDecorator('system', {
                initialValue: initialValue.system,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select >
                  <Option value="1">Đại học tính chỉ</Option>
                  <Option value="2">Cao đẳng tính chỉ</Option>
                  <Option value="3">Trung cấp tính chỉ</Option>
                </Select>)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Xếp loại" hasFeedback>
              {getFieldDecorator('nameRating', {
                initialValue: initialValue.nameRating,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Mã xếp loại" hasFeedback>
              {getFieldDecorator('codeRating', {
                initialValue: initialValue.codeRating,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm HT 10" hasFeedback>
              {getFieldDecorator('min10', {
                initialValue: initialValue.min10,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm HT 4" hasFeedback>
              {getFieldDecorator('min4', {
                initialValue: initialValue.min4,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm RL" hasFeedback>
              {getFieldDecorator('minTrainingPoints', {
                initialValue: initialValue.minTrainingPoints,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số tiền" hasFeedback>
              {getFieldDecorator('totalMoney', {
                initialValue: initialValue.totalMoney,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel />
            </Form.Item>
          </Form>
        </Row>
      </Card >
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormScholarshipRatingsPage {...props} />
  </Page>
));



